.awesomplete [hidden] {
  display: none;
}
.awesomplete .visually-hidden {
  position: absolute;
  clip: rect(0, 0, 0, 0);
}
.awesomplete {
  display: inline-block;
  position: relative;
}
.awesomplete > input {
  display: block;
}
.awesomplete > ul {
  position: absolute;
  left: 0;
  z-index: 1;
  min-width: 100%;
  box-sizing: border-box;
  list-style: none;
  padding: 0;
  margin: 0;
  background: #fff;
}
.awesomplete > ul:empty {
  display: none;
}
.awesomplete > ul {
  border-radius: .3em;
  margin: .2em 0 0;
  background: rgba(255, 255, 255, 0.9);
  background: linear-gradient(to bottom right, white, rgba(255, 255, 255, 0.8));
  border: 1px solid rgba(0, 0, 0, 0.3);
  box-shadow: 0.05em 0.2em 0.6em rgba(0, 0, 0, 0.2);
  text-shadow: none;
}
@supports (transform: scale(0)) {
  .awesomplete > ul {
    transition: 0.3s cubic-bezier(0.4, 0.2, 0.5, 1.4);
    transform-origin: 1.43em -0.43em;
  }
  .awesomplete > ul[hidden],
  .awesomplete > ul:empty {
    opacity: 0;
    transform: scale(0);
    display: block;
    transition-timing-function: ease;
  }
}
/* Pointer */
.awesomplete > ul:before {
  content: "";
  position: absolute;
  top: -0.43em;
  left: 1em;
  width: 0;
  height: 0;
  padding: .4em;
  background: white;
  border: inherit;
  border-right: 0;
  border-bottom: 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.awesomplete > ul > li {
  position: relative;
  padding: .2em .5em;
  cursor: pointer;
}
.awesomplete > ul > li:hover {
  background: #b8d3e0;
  color: black;
}
.awesomplete > ul > li[aria-selected="true"] {
  background: #3d6d8f;
  color: white;
}
.awesomplete mark {
  background: #eaff00;
}
.awesomplete li:hover mark {
  background: #b5d100;
}
.awesomplete li[aria-selected="true"] mark {
  background: #3d6b00;
  color: inherit;
}
