  display: table;
  content: " ";
}
.job:after {
  clear: both;
}
.job .subhead {
  font-size: 19px;
  font-size: 1.1875rem;
  font-weight: bold;
  color: #898375;
}
.job__figure {
  margin-bottom: 1.4em;
  margin-bottom: 1.4rem;
  text-align: center;
}
.category-menu .active {
  font-weight: bold;
}
.job-image {
  float: right;
  max-height: 300px;
}
.job-detail {
  width: 100%;
  clear: both;
  margin: .5em auto;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
  *zoom: 1;
}
.job-detail:before,
.job-detail:after {
  display: table;
  content: " ";
}
.job-detail:after {
  clear: both;
}
.job-detail span {
  display: block;
  clear: both;
}
.job_text {
  width: auto;
  float: left;
}
.job_icon {
  width: 50px;
  color: #BCC659;
  float: left;
  margin-right: .5em;
}
.apply {
  margin-top: 1em;
  text-align: center;
  width: 100%;
}
@media only screen and (min-width: 37.5em) {
  .job {
    display: flex;
  }
  .job__figure {
    float: left;
    width: 23%;
    margin-right: 4%;
  }
  .job__body {
    float: right;
    width: 73%;
    flex-grow: 1;
  }
}
/* == WordPress WYSIWYG Editor Styles == */
.entry-content img,
.single .wp-post-image {
  margin-bottom: 1.4em;
}
.alignleft,
img.alignleft {
  margin-right: 1.5em;
  display: inline;
  float: left;
}
.alignright,
img.alignright {
  margin-left: 1.5em;
  display: inline;
  float: right;
}
.aligncenter,
img.aligncenter {
  margin-right: auto;
  margin-left: auto;
  display: block;
  clear: both;
}
.wp-caption {
  margin-bottom: 1.5em;
  padding-top: 5px;
}
.wp-caption img {
  border: 0 none;
  padding: 0;
  margin: 0;
}
.wp-caption .wp-caption-text {
  font-size: 14px;
  font-size: 0.875rem;
  line-height: 1.4em;
  margin: 0.4 em 0 0;
}
.wp-smiley {
  margin: 0 !important;
  max-height: 1em;
}
blockquote.left {
  margin-right: 20px;
  text-align: right;
  margin-left: 0;
  width: 33%;
  float: left;
}
blockquote.right {
  margin-left: 20px;
  text-align: left;
  margin-right: 0;
  width: 33%;
  float: right;
}
