}
@media only screen and (min-width: 48em) {
  .home__col {
    float: left;
    width: 46%;
    margin-right: 2%;
    margin-left: 2%;
  }
  .home__col:nth-of-type(1n) {
    width: 48%;
    margin-right: 4%;
    margin-left: 0;
  }
  .home__col:nth-of-type(2n) {
